.quiz-image-box {
  border-radius: 3px;
  box-shadow:
    0 2px 3px rgba(10, 10, 10, 0.1),
    0 0 0 1px rgba(10, 10, 10, 0.1);
  overflow: hidden;
  position: relative;
}

.answers {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.answers-vertical {
  flex-direction: column;
}

.answer {
  margin-top: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 49%;
}

.answers-vertical .answer {
  width: 100%;
}

.quiz-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 2;
}

.quiz-image-fill {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(15px) brightness(0.7);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transform: scale(1.05);
  width: 100%;
  z-index: 1;
}

.quiz-overlay {
  align-items: center;
  animation: fade-in 300ms forwards;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 0;
  transition: background 300ms ease-out;
  width: 100%;
  z-index: 3;
}

.quiz-overlay:hover {
  background: rgba(0, 0, 0, 0.5);
}

.quiz-overlay a {
  color: #fff;
}

.quiz-button {
  background: transparent;
  border: none;
  color: #eee;
  cursor: pointer;
  font-size: 6em;
  text-shadow: 0 0 50px rgba(0, 0, 0, 0.6);
  transition: color 300ms ease-out;
}

.quiz-button:hover {
  color: #fff;
}

.quiz-next {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: flex-end;
  width: 100%;
}
